.productdisplay {
  display: flex;
  margin: 0px 130px;
}
.productdisplay-left {
  display: flex;
  gap: 15px;
}
.productdisplay-img-list {
  display: flex;
  flex-direction: column;
  gap: 14px;
}
.productdisplay-img-list img {
  height: 110px;
}
.productdisplay-main-img {
  width: 400px;
  height: 500px;
}
.productdisplay-right {
  margin: 0 40px;
  display: flex;
  flex-direction: column;
}
.productdisplay-right h1 {
  color: #3d3d3d;
  font-size: 30px;
  font-weight: 700;
}
.productdisplay-right-stars {
  display: flex;
  align-items: center;
  margin-top: 8px;
  gap: 4px;
  color: #1c1c1c;
  font-size: 12px;
}
.productdisplay-right-prices {
  display: flex;
  margin: 20px 0px;
  gap: 15px;
  font-size: 18px;
  font-weight: 700;
}
.productdisplay-right-price-old {
  color: #818181;
  text-decoration: line-through;
}
.productdisplay-right-price-new {
  color: #ff4141;
}
.productdisplay-right-size h1 {
  margin-top: 15px;
  color: #656565;
  font-size: 12px;
  font-weight: 600;
}
.productdisplay-right-sizes {
  display: flex;
  margin: 10px 0px;
  gap: 16px;
}
.productdisplay-right-sizes div.active {
  border: 1px solid #f44336;
}
.productdisplay-right-sizes div {
  padding: 6px 13px;
  background: #fbfbfb;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  cursor: pointer;
}
.productdisplay-right button {
  padding: 8px 10px;
  width: 199px;
  font-size: 16px;
  font-weight: 600;
  color: white;
  background: #ff4141;
  margin-bottom: 40px;
  border: none;
  outline: none;
  cursor: pointer;
}
.productdisplay-right-category {
  margin-top: 8px;
}
.productdisplay-right-category span {
  font-weight: 600;
}
