.footer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-top: 130px;
}
.footer-logo{
  display: flex;
  align-items: center;
  gap: 10px;
}
.footer-logo p{
  color: #383838;
  font-size: 40px;
  font-weight: 700;
}
.footer-links{
  display: flex;
  list-style: none;
  gap: 40px;
  color: #252525;
  font-size:16px;
}
.footer li{
  cursor: pointer;
}
.footer-social-icon{
  display: flex;
  gap: 15px;
}
.footer-icons-container{
  padding: 8px;
  padding-bottom: 4px;
  background: #fbfbfb;
  border: 1px solid #ebebeb;
}
.footer-copyright{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  margin-bottom: 25px;
  color: #1a1a1a;
  font-size: 16px;
}
.footer-copyright hr{
  width: 80%;
  border: none;
  border-radius: 10px;
  height: 3px;
  background: #7c7c7c;
}