.item{
  width: 250px;
}
.item-image{
  height: 300px;
  width: 250px;
}
.item p{
  margin: 2px 0; 
}
.item-prices{
  display: flex;
  gap: 15px;
}
.item-price-new{
  color: #374151;
  font-size: 16px;
  font-weight: 600;
}
.item-price-old{
  color: #8c8c8c;
  font-size: 16px;
  font-weight: 500;
  text-decoration: line-through;
}
.item:hover{
  scale: 1.05;
  transition: 0.6s;
}