.loginsignup {
  width: 100%;
  height: 80vh;
  background: #fce3fe;
  padding-top: 40px;
}
.loginsignup-container {
  width: 400px;
  background: white;
  margin: -30px auto;
  padding: 10px 50px;
  border-radius: 10px;
}
.loginsignup-container h1 {
  margin: 10px 0px;
}
.loginsignup-fields {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}
.loginsignup-fields input {
  height: 55px;
  width: 100%;
  padding-left: 15px;
  border: 1px solid #c9c9c9;
  outline: none;
  color: #5c5c5c;
  font-size: 14px;
}
.loginsignup-container button {
  width: 420px;
  height: 60px;
  color: white;
  background: #ff4141;
  margin-top: 20px;
  border: none;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
}
.loginsignup-login {
  margin-top: 10px;
  color: #5c5c5c;
  font-size: 14px;
  font-weight: 500;
}
.loginsignup-login a {
  color: #ff4141;
  font-weight: 600;
}
.loginsignup-agree {
  display: flex;
  align-items: center;
  margin-top: 5px;
  gap: 16px;
  color: #5c5c5c;
  font-size: 13px;
  font-weight: 500;
}
.info {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  width: 90%;
  padding: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  background: #509af8;
  border-radius: 8px;
  box-shadow: 0px 0px 5px -3px #111;
  margin: 8px 0;
}

.info__icon {
  width: 15px;
  height: 15px;
  transform: translateY(-2px);
  margin-right: 8px;
}

.info__icon path {
  fill: #fff;
}

.info__title {
  font-weight: 500;
  font-size: 1.2rem;
  color: #fff;
}

.info__close {
  width: 10px;
  height: 10px;
  cursor: pointer;
  margin-left: auto;
}

.info__close path {
  fill: #fff;
}