.hero {
  height: 85vh;
  background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
  display: flex;
}
.hero-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 16px;
  padding-left: 160px;
  line-height: 1.1;
}
.hero-left h2 {
  color: #090909;
  font-size: 22px;
  font-weight: 600;
}
.hero-left p {
  color: #171717;
  font-size: 55px;
  font-weight: 700;
}
.hero-hand-icon {
  display: flex;
  align-items: center;
  gap: 15px;
}
.hero-hand-icon img {
  width: 90px;
}
.hero-latest-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  width: 254px;
  height: 45px;
  border-radius: 22px;
  margin-top: 20px;
  background: #ff4141;
  color: white;
  font-size: 16px;
  font-weight: 500;
}
.hero-right img{
  width: 370px;
}
.hero-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
