.navbar {
  display: flex;
  justify-content: space-around;
  padding: 2px 93px;
  box-shadow: 0 1px 3px -2px black;
}
.nav-logo {
  display: flex;
  align-items: center;
  gap: 8px;
}
.nav-logo p {
  color: #171717;
  font-size: 35px;
  font-weight: 600;
}
.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 46px;
  color: #626262;
  font-size: 18px;
  font-weight: 500;
}
.nav-menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  cursor: pointer;
}
.nav-menu hr {
  border: none;
  width: 70%;
  height: 2px;
  border-radius: 8px;
  background: #ff4141;
}
.nav-login-cart {
  display: flex;
  align-items: center;
  gap: 40px;
}
.nav-login-cart button {
  width: 150px;
  height: 50px;
  outline: none;
  border: 1px solid #7a7a7a;
  border-radius: 60px;
  color: #515151;
  font-size: 20px;
  font-weight: 500;
  background: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.nav-login-cart button:active {
  background: #f3f3f3;
}
.nav-login-cart a{
  text-decoration: none;
}
.nav-cart-count {
  width: 22px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -35px;
  margin-left: -55px;
  border-radius: 11px;
  font-size: 14px;
  background: red;
  color: white;
}
