.newsletter{
  width: 65%;
  height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 0px 120px;
  margin-bottom: 130px;
  background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
  gap: 20px;
}
.newsletter h1{
  color: #454545;
  font-size: 40px;
  font-weight: 600;
}
.newsletter p{
  color: #454545;
  font-size: 16px;
}
.newsletter div{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  width: 700px;
  height: 50px;
  border-radius: 60px;
  border: 1px solid #e3e3e3;
}
.newsletter input{
  width: 450px;
  margin-left: 20px;
  border: none;
  outline: none;
  color: #616161;
  font-family:Poppins;
  font-size: 14px;
}
.newsletter div button{
  width: 200px;
  height: 50px;
  border-radius: 80px;
  background: black;
  color: white;
  font-size: 12px;
  cursor: pointer;
}