@media only screen and (max-width: 600px) {
  .loginsignup-container {
    width: 90%;
    margin: 10px auto; /* Adjust margin for smaller screens */
    padding: 15px; /* Adjust padding for smaller screens */
  }

  .loginsignup-fields input {
    height: 40px; /* Adjust height for smaller screens */
  }

  .loginsignup-container button {
    width: 100%;
    height: 50px; /* Adjust height for smaller screens */
  }
  .loginsignup {
    width: 100%;
    height: auto;
    background: #fce3fe;
    padding-top: 20px;
  }

  .loginsignup-container {
    width: 90%;
    margin: 20px auto;
    padding: 20px;
  }

  .loginsignup-fields input {
    height: 45px;
  }

  .loginsignup-container button {
    width: 100%;
  }
  .navbar {
    display: flex;
    flex-direction: column; /* Change to column layout for mobile */
    align-items: center; /* Center items for mobile */
    padding: 10px; /* Adjust padding for mobile */
    box-shadow: 0 1px 3px -2px black;
  }

  .nav-logo {
    margin-bottom: 10px; /* Add margin for separation in mobile */
  }

  .nav-logo p {
    font-size: 24px; /* Adjust font size for mobile */
  }

  .nav-menu {
    display: flex;
    flex-direction: column; /* Change to column layout for mobile */
    align-items: center; /* Center items for mobile */
    list-style: none;
    gap: 20px; /* Adjust gap for mobile */
    color: #626262;
    font-size: 18px;
    font-weight: 500;
  }

  .nav-menu li {
    cursor: pointer;
  }

  .nav-menu hr {
    display: none; /* Hide hr for mobile */
  }

  .nav-login-cart {
    display: flex;
    flex-direction: column; /* Change to column layout for mobile */
    align-items: center; /* Center items for mobile */
    gap: 20px; /* Adjust gap for mobile */
  }

  .nav-login-cart button {
    width: 100%; /* Make button full width for mobile */
  }

  .nav-login-cart a {
    text-align: center; /* Center text for mobile */
  }

  .nav-cart-count {
    margin: 0; /* Remove negative margins for mobile */
    margin-top: 5px; /* Adjust top margin for mobile */
    margin-left: 0; /* Remove left margin for mobile */
  }
  .item {
    width: 100%; /* Make items full width for mobile */
  }

  .item-image {
    width: 100%; /* Make images full width for mobile */
    max-height: 300px; /* Set a maximum height for images */
  }

  .item p {
    margin: 2px 0;
  }

  .item-prices {
    display: flex;
    flex-direction: column; /* Change to column layout for mobile */
    gap: 5px; /* Adjust gap for mobile */
  }

  .item-price-new {
    color: #374151;
    font-size: 16px;
    font-weight: 600;
  }

  .item-price-old {
    color: #8c8c8c;
    font-size: 16px;
    font-weight: 500;
    text-decoration: line-through;
  }

  /* Add a hover effect for touch devices (optional) */
  @media only screen and (hover: none) {
    .item:hover {
      scale: 1; /* Remove scaling for touch devices */
      transition: none; /* Remove transition for touch devices */
    }
  }
  .relatedproducts {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 20px; /* Add padding for better spacing on mobile */
  }

  .relatedproducts h1 {
    color: #171717;
    font-size: 36px; /* Adjust font size for mobile */
    font-weight: 600;
  }

  .relatedproducts hr {
    width: 100%; /* Make hr full width for mobile */
    max-width: 200px; /* Set a maximum width for hr on larger screens */
    height: 6px;
    border-radius: 10px;
    background: #252525;
    margin: 10px 0; /* Adjust margin for better spacing on mobile */
  }

  .relatedproducts-item {
    margin-top: 20px; /* Adjust top margin for better spacing on mobile */
    display: flex;
    flex-direction: column; /* Change to column layout for mobile */
    gap: 10px; /* Adjust gap for better spacing on mobile */
  }

  /* Media query for screens smaller than 600px (adjust as needed) */
  .relatedproducts h1 {
    font-size: 24px; /* Adjust font size for smaller screens */
  }

  .relatedproducts-item {
    gap: 15px; /* Adjust gap for better spacing on smaller screens */
  }
  .hero {
    height: 85vh;
    background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
    display: flex;
    flex-direction: column; /* Change to column layout for mobile */
    align-items: center; /* Center items for mobile */
  }

  .hero-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 16px;
    padding: 0 20px; /* Adjust padding for mobile */
    text-align: center; /* Center text for mobile */
  }

  .hero-left h2 {
    font-size: 18px; /* Adjust font size for mobile */
    font-weight: 600;
  }

  .hero-left p {
    font-size: 32px; /* Adjust font size for mobile */
    font-weight: 700;
  }

  .hero-hand-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
  }

  .hero-hand-icon img {
    width: 60px; /* Adjust image size for mobile */
  }

  .hero-latest-btn {
    width: 100%; /* Make button full width for mobile */
    margin-top: 20px;
  }

  .hero-right img {
    width: 100%; /* Make image full width for mobile */
  }

  .hero-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  /* Media query for screens smaller than 600px (adjust as needed) */
  .hero-left {
    padding: 20px; /* Adjust padding for smaller screens */
  }

  .hero-left h2 {
    font-size: 16px; /* Adjust font size for smaller screens */
  }

  .hero-left p {
    font-size: 24px; /* Adjust font size for smaller screens */
  }

  .hero-hand-icon img {
    width: 40px; /* Adjust image size for smaller screens */
  }
}
